html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  overflow-x: hidden;
}

.container {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  width: 100%;
  min-width: 100%;
}

.navbar {
  width: 100%;
  text-align: right;
}

.home-main {
  width: 100%;
}

.clients-main {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
  row-gap: 10px;
}

.row {
  padding: 20% 5% 20% 5%;
}

.col {
  text-align: center;
}

.tan {
  background-color: #FBECC1;
}

.green {
  background-color: #8D8741;
}

.red {
  background-color: #DAAD86;
}

.image {
  height: 120px;
  width: auto;
}